<template>
    <div class="page-wrapper">
        <navbar />
        <div class="main-container">
            <el-breadcrumb style="margin-bottom: 20px" separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>{{setInfo.name}}</el-breadcrumb-item>
            </el-breadcrumb>
            <el-card>
                <el-table :data="exercises">
                    <el-table-column
                            label="题目名称"
                            prop="name"/>
                    <el-table-column>
                        <template slot-scope="scope">
                            <router-link :to="'/exerciseSets/' + $route.params.id + '/' + scope.row.id">前往练习</router-link>
                        </template>
                    </el-table-column>
                </el-table>
            </el-card>
        </div>
    </div>
</template>

<script>
    import Navbar from "../../components/Navbar";
    import {fetchExercisesApi, fetchExerciseSetDetailApi} from "../../requests/exercises";
    export default {
        name: "SetDetail",
        components: {Navbar},
        data(){
           return {
               exercises:[],
               setInfo:{}
           }
        },
        mounted(){
            fetchExercisesApi({
                sid:this.$route.params.id,
                all:true
            }).then(response=>{
                this.exercises = response.data.results
            })

            fetchExerciseSetDetailApi(this.$route.params.id).then(response=>{
                this.setInfo = response.data
            })
        }
    }
</script>

