<template>
    <div class="page-wrapper">
        <navbar />

        <div class="main-container">
            <el-row>
                <el-col v-for="item in exerciseSets" :span="8">
                    <router-link :to="'/exerciseSets/' + item.id">
                        <el-card >
                            {{item.name}}
                        </el-card>
                    </router-link>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    import Navbar from "../../components/Navbar";
    import {fetchExerciseSetsApi} from "../../requests/exercises";
    export default {
        name: "Sets",
        components: {Navbar},
        data(){
            return {
                exerciseSets:[]
            }
        },
        mounted(){
            fetchExerciseSetsApi().then(response=>{
                this.exerciseSets = response.data.results
            })
        }
    }
</script>

