<template>
    <div>
        <navbar/>

        <el-form v-if="!success"
                 class="main-form"
                 :rules="rules"
                 :model="form"
                 ref="form">
            <el-form-item prop="email">
                <el-input v-model="form.email"
                          placeholder="请输入你的邮箱"/>
            </el-form-item>
            <el-form-item prop="password">
                <el-input v-model="form.password"
                          type="password"
                          placeholder="请设置你的密码"/>
            </el-form-item>
            <el-form-item>
                <el-button
                        :loading="loading"
                        @click="submit"
                        style="width: 100%"
                        type="primary">
                    注册
                </el-button>
            </el-form-item>
            <el-alert
                    type="error"
                    :closable="false"
                    v-if="error"
                    :title="error"/>
        </el-form>
        <div v-else class="main-form" style="text-align: center">
            <img src="../../assets/images/email-success.png"/>
            <h3>激活邮件发送成功</h3>
        </div>
    </div>
</template>

<script>
    import Navbar from "../../components/Navbar";
    import {signUpApi} from "../../requests/user";

    export default {
        name: "SignUp",
        components: {Navbar},
        data() {
            return {
                form: {
                    email: "",
                    password: ''
                },
                success: false,
                error:"",
                loading:false,
                rules: {
                    email: [
                        {type:"email", message:"请输入正确的邮箱", trigger:"blur"},
                        {required: true, message: "请输入邮箱"}
                    ],
                    password: [
                        {required: true, message: "请输入密码"}
                    ]
                }
            }
        },
        methods: {
            submit() {
                this.error = ""
                this.$refs["form"].validate(valid => {
                    if (!valid) return;
                    this.loading = true
                    signUpApi(this.form).then(response => {
                        this.success = true
                        this.loading = false
                    }).catch(err=>{
                        this.error = err.response.data.msg
                        this.loading = false
                    })
                })
            }
        }
    }
</script>

