<template>
    <div>
       <navbar />

        <el-form
                ref="form"
                :model="form"
                :rules="rules"
                class="main-form">
            <el-form-item prop="username">
                <el-input
                        placeholder="请输入用户名"
                        v-model="form.username"/>
            </el-form-item>
            <el-form-item prop="password">
                <el-input
                        placeholder="请输入密码"
                        type="password"
                        v-model="form.password"/>
            </el-form-item>
            <el-form-item>
                <el-button
                        @click="submit"
                        class="submit"
                        type="primary">登录</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import Navbar from "../../components/Navbar";
    import {loginApi} from "../../requests/user";
    export default {
        name: "SignIn",
        data(){

            return {
                form:{
                    username:"",
                    password:""
                },
                rules:{
                    username:[
                        {required:true, message:"请输入用户名"}
                    ],
                    password:[
                        {required:true, message:"请输入密码"}
                    ]
                }
            }
        },
        components: {Navbar},
        methods:{
            submit(){
                this.$refs["form"].validate(valid=>{
                    if(!valid) return;
                    loginApi(this.form).then(response=>{
                        this.$message.success("登录成功")
                        this.$store.commit("saveUserInfo", response.data)
                    })
                })
            }
        }
    }
</script>

